/* Plant Type Carousel Styles */

.spinner {
  @apply w-8 h-8 border-2 border-gray-300 border-t-primary-600 rounded-full animate-spin;
}

/* Carousel container */
.carousel-container {
  @apply relative overflow-hidden rounded-3xl shadow-2xl;
}

/* Slide transitions */
.slide {
  @apply absolute inset-0 transition-all duration-700 ease-in-out;
}

.slide.active {
  @apply opacity-100 transform translate-x-0;
}

.slide.inactive {
  @apply opacity-0;
}

.slide.next {
  @apply transform translate-x-full;
}

.slide.prev {
  @apply transform -translate-x-full;
}

/* Navigation buttons */
.nav-button {
  @apply absolute top-1/2 -translate-y-1/2 w-12 h-12 bg-white/20 backdrop-blur-sm rounded-full flex items-center justify-center text-white hover:bg-white/30 transition-all duration-200;
}

.nav-button:hover {
  @apply scale-110;
}

.nav-button.prev {
  @apply left-6;
}

.nav-button.next {
  @apply right-6;
}

/* Slide indicators */
.indicator {
  @apply w-3 h-3 rounded-full transition-all duration-200 cursor-pointer;
}

.indicator.active {
  @apply bg-white scale-125;
}

.indicator.inactive {
  @apply bg-white/50 hover:bg-white/75;
}

/* Image gallery */
.image-gallery {
  @apply grid grid-cols-2 gap-4;
}

.gallery-image {
  @apply aspect-square rounded-2xl overflow-hidden shadow-lg;
}

.gallery-image img {
  @apply w-full h-full object-cover hover:scale-110 transition-transform duration-300;
}

/* Mobile gallery */
.mobile-gallery {
  @apply flex gap-4 overflow-x-auto pb-4;
}

.mobile-gallery-item {
  @apply flex-shrink-0 w-32 h-32 rounded-2xl overflow-hidden shadow-lg;
}

.mobile-gallery-item img {
  @apply w-full h-full object-cover;
}

/* Auto-play indicator */
.auto-play-indicator {
  @apply absolute top-6 right-6 bg-black/20 backdrop-blur-sm rounded-full px-3 py-1;
}

/* Responsive adjustments */
@media (max-width: 1024px) {
  .nav-button {
    @apply w-10 h-10;
  }
  
  .nav-button.prev {
    @apply left-4;
  }
  
  .nav-button.next {
    @apply right-4;
  }
}

@media (max-width: 768px) {
  .nav-button {
    @apply w-8 h-8;
  }
  
  .nav-button svg {
    @apply w-4 h-4;
  }
}

/* Smooth scrolling for mobile gallery */
.mobile-gallery {
  scroll-behavior: smooth;
  -webkit-overflow-scrolling: touch;
}

/* Custom scrollbar for mobile gallery */
.mobile-gallery::-webkit-scrollbar {
  height: 4px;
}

.mobile-gallery::-webkit-scrollbar-track {
  @apply bg-gray-100 rounded-full;
}

.mobile-gallery::-webkit-scrollbar-thumb {
  @apply bg-gray-300 rounded-full hover:bg-gray-400;
}

/* Loading animation */
@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
}

.animate-pulse {
  animation: pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
}

/* Hover effects */
.hover-scale {
  @apply transition-transform duration-300;
}

.hover-scale:hover {
  @apply scale-105;
}

/* Text shadows for better readability */
.text-shadow {
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
}

/* Backdrop blur effects */
.backdrop-blur {
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
}

/* Gradient overlays */
.gradient-overlay {
  @apply absolute inset-0 bg-gradient-to-r from-black/60 via-black/30 to-transparent;
}

/* Focus states for accessibility */
.nav-button:focus,
.indicator:focus {
  @apply outline-none ring-2 ring-white ring-offset-2 ring-offset-transparent;
}

/* Reduced motion support */
@media (prefers-reduced-motion: reduce) {
  .slide,
  .nav-button,
  .indicator,
  .gallery-image img {
    @apply transition-none;
  }
  
  .animate-pulse {
    animation: none;
  }
} 